import { DateTime } from "luxon";

const API_KEY = "c09565c6795c5837d9c9a42978526e4d";

const BASE_URL = "https://api.openweathermap.org/data/2.5";

const getWeatherData = (infoType, searchParams) => {
    const url = new URL(BASE_URL + '/' + infoType)
    console.log(url)
    url.search = new URLSearchParams({ ...searchParams, appid: API_KEY })

    return fetch(url).then((res) => res.json());
};

// export default getWeatherData;

const formatForecastedWeather = (data) => {
    let { timezone, daily, hourly } = data
    daily = daily?.slice(1, 6).map(d => {
        return {
            title: formatToLocalTime(d.dt, timezone, 'ccc'),
            temp: d.temp.day,
            icon: d.weather[0].icon
        }
    });
    hourly = hourly?.slice(1, 6).map(d => {
        return {
            title: formatToLocalTime(d.dt, timezone, 'hh:mm a'),
            temp: d.temp,
            icon: d.weather[0].icon
        }
    });

    return { timezone, daily, hourly }

}

const formatCurrentWeather = (data) => {
    const {
        coord: { lat, lon },
        main: { temp, feels_like, temp_min, temp_max, humidity },
        name,
        dt,
        sys: { country, sunrise, sunset },
        weather,
        wind: { speed, deg }
    } = data

    const { main: details, icon } = weather[0]

    return {
        lat, lon, temp, feels_like, temp_min, temp_max, humidity, name,
        dt, country, sunrise, sunset, weather, speed, deg, details, icon
    }
}
const getFormattedWeatherData = async (searchParams) => {
    const formattedCurrentWeather = await getWeatherData('weather', searchParams).then(formatCurrentWeather)

    const { lat, lon } = formattedCurrentWeather;

    const forecastedWeatherData = await getWeatherData('onecall', {
        lat, lon, exclude: 'current,minutely,alerts', units: searchParams.units
    }).then(formatForecastedWeather)

    return { ...formattedCurrentWeather, ...forecastedWeatherData }
};

const formatToLocalTime = (secs, zone, format = "cccc, dd LLL yyyy' | Local time: 'hh:mm a") => DateTime.fromSeconds(Number(secs)).setZone(zone).toFormat(format)

const iconUrlFromCode = (code) =>`http://openweathermap.org/img/wn/${code}@2x.png`


export default getFormattedWeatherData;

export {formatToLocalTime, iconUrlFromCode}